function compare(a, b) {
    if (a.address > b.address) return 1
    else return -1
}
export const data = db.split('\n').map(row => {
  const attributes = row.split(',')
  return {
    id: attributes[0],
    description: attributes[1],
    startDate: attributes[2],
    endDate: attributes[3],
    address: attributes[4],
    name: attributes[5],
    lat: Number(attributes[6]),
    lng: Number(attributes[7]),
  }
}).slice(1).filter(x => !(x.address.match(/^\d/))).sort(compare)